@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: #000;
  --secondary-color: #3d3d3d;
  --tertiary-color: #dedede;
  --primary-bg: #ffffff;
  --hover-primary-color: #161616;
  --cherry-color: #ffe0b5b3;
}

body {
  max-width: 1140px;
  margin: auto;
  background-color: #ffffff;
  background-color: var(--primary-bg);
}

.section-heading {
  font-size: clamp(40px, 5vw, 60px);
  margin: 5rem;
  font-weight: 600;
  color: #3d3d3d;
  color: var(--secondary-color);
  line-height: 1.1;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  margin-top: 10rem;
}

.sub-heading {
  color: #000;
  color: var(--primary-color);
}

.text {
  color: var(--text-color);
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}

.d-flex {
  display: flex;
}

.cta-btn {
  border: 2px solid #000;
  border: 2px solid var(--primary-color);
  color: #000;
  color: var(--primary-color);

  font-size: 16px;
  font-weight: bold;
  border-radius: 25px;
  background: transparent;
  width: 116px;
  height: 50px;
  cursor: pointer;
  letter-spacing: 0.1rem;
}

.cta-btn-outline,
.cta-btn-filled {
  margin-right: 2rem;
}

.cta-btn-outline:hover,
.cta-btn-filled:hover {
  background-color: #161616;
  background-color: var(--hover-primary-color);
  color: #fff;
}

.cta-btn-filled {
  border: 2px solid transparent;
  color: #ffffff;
  color: var(--primary-bg);
  padding: 16px 32px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 4px;
  background: #000;
  background: var(--primary-color);
  height: auto;
  cursor: pointer;
  letter-spacing: 0.1rem;
}

.cta-btn-outline {
  border: 2px solid #000;
  border: 2px solid var(--primary-color);
  color: #000;
  color: var(--primary-color);
  padding: 16px 32px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 4px;
  background: transparent;
  height: auto;
  cursor: pointer;
  letter-spacing: 0.1rem;
}

.modal-close-btn {
  display: block;
  margin-left: auto;
  background: transparent;
  border: none;
  font-size: 35px;
}
.d-flex {
  display: flex;
}

.text-center {
  text-align: center;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.sm-icons {
  display: none;
}

.see-full-list-btn {
  margin: 1rem auto;
  display: block;
}

@media only screen and (max-width: 1600px) {
  .cta-btn-outline,
  .cta-btn-filled {
    margin-bottom: 1rem;
  }
}

@media only screen and (max-width: 768px) {
  .section-heading {
    margin: 5rem auto 1rem auto;
  }
}

@media only screen and (min-width: 1600px) {
  .sm-icons {
    display: block;
    position: fixed;
    top: 75vh;
    left: 150px;
    text-align: center;
  }

  .sm-icons a {
    text-decoration: none;
  }
  .sm-icons .fa {
    display: block;
    font-size: 20px;
    margin-bottom: 15px;
    color: #000;
    color: var(--primary-color);
  }

  .sm-icons .long-line {
    height: 20vh;
    width: 2px;
    border-radius: 4px;
    background-color: #000;
    background-color: var(--primary-color);
    margin-left: 6px;
    margin-right: 50px;
    margin: auto;
  }
}

.scrollToTop {
  display: none;
}
.scrollToTop.active {
  display: inline;
  position: fixed;
  cursor: pointer;
  bottom: 0px;
  height: 45px;
  border-radius: 100%;
  /* background: #f8f7ff; */
  margin: 1rem;
  right: 0;
}

.Modal{
  position: absolute;
  inset: 40px;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 10px;
}
.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.timeline:before {
  content: "";
  position: absolute;
  height: 100%;
  left: 50%;
  width: 2px;
  top: -20px;
  background: var(--secondary-color);
  /* z-index: 1; */
}

.timeline {
  position: relative;
  margin: 50px auto;
  width: 1000px;
  padding: 50px 0;
  color: var(--primary-color);
}

.timeline ul {
  margin: 0;
  padding: 0;
}

.timeline ul li {
  list-style: none;
  box-sizing: border-box;
  line-height: normal;
  position: relative;
  width: 50%;
  
  padding: 0 70px 40px;
}

.timeline ul li .right_content h2 {
  color: var(--primary-color);
  /* padding: 0px 2px 18px 0px; */
}

.timeline ul li:nth-child(odd) {
  float: left;
  text-align: right;
  clear: both;
}

.timeline ul li:nth-child(even) {
  float: right;
  text-align: left;
  clear: both;
}

.left_content {
  padding-bottom: 20px;
}

.timeline ul li:nth-child(odd):before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background: var(--primary-color);
  border-radius: 50%;
  border: 2px solid var(--tertiary-color);
  right: -9px;
  top: 24px;
  /* z-index: 1; */
}

.timeline ul li:nth-child(even):before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background: var(--primary-color);
  border-radius: 50%;
  border: 2px solid var(--tertiary-color);
  left: -7px;
  top: 24px;
  /* z-index: 1; */
}

.timeline ul li:nth-child(odd) .left_content {
  position: absolute;
  top: 0px;
  right: -240px;
  margin: 0;
  padding: 0 32px;

  border: 2px solid var(--tertiary-color);
  border-radius: 50px;
}

.timeline ul li:nth-child(even) .left_content {
  position: absolute;
  top: 0px;
  left: -200px;
  margin: 0;
  padding: 0 32px;

  border: 2px solid var(--tertiary-color);
  border-radius: 50px;
}

.timeline h3.company-span{
  color: var(--text-color);
}

@media (max-width: 1000px) {
  .timeline {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .timeline {
    width: 100%;
  }
  .timeline:before {
    left: 20px;
  }
  .timeline ul li:nth-child(odd),
  .timeline ul li:nth-child(even) {
    width: 100%;
    text-align: left;
    padding-left: 50px;
  }

  .timeline ul li:nth-child(odd):before {
    top: -18px;
    left: 13px;
  }
  .timeline ul li:nth-child(even):before {
    top: -18px;
    left: 13px;
  }

  .timeline ul li:nth-child(odd) .left_content,
  .timeline ul li:nth-child(even) .left_content {
    top: -30px;
    left: 50px;
    right: inherit;
  }

  .timeline ul li{
    padding: 40px 70px;
  }
}

